<template>
  <el-main>
    <div class="wrap-header">
      <div class="item"><span>总地址数：</span>{{this.totalAccount}}</div>
      <div class="item"><span>24小时新增：</span>{{this.newAccount}}</div>
    </div>
    <h3>交互记录分析</h3>
    <div class="header-input">
      <el-input v-model="wallet" placeholder="请输入钱包地址"></el-input><el-button type="primary" @click="fetchData">查询</el-button>
    </div>
    <div  v-show="addrData">
      <div class="wrap-data">
        <el-row :gutter="18" class="row-1">
          <el-col :span="6">
            <el-card>
              <div class="card-label">上次活跃</div>
              <div class="card-data">{{ addrData.srecent }}</div>
            </el-card>
          </el-col>
          <el-col :span="6">
            <el-card>
              <div class="card-label">Tx</div>
              <div class="card-data">{{ addrData.eraTx }}</div>
            </el-card>
          </el-col>
          <el-col :span="6">
            <el-card>
              <div class="card-label">合约</div>
              <div class="card-data">{{ addrData.contract }}</div>
            </el-card>
          </el-col>
          <el-col :span="6">
            <el-card>
              <div class="card-label">gas_eth</div>
              <div class="card-data">{{ addrData.fee }}</div>
            </el-card>
          </el-col>
        </el-row>
        <el-row :gutter="18" class="row-2">
          <el-col :span="6">
            <el-card>
              <div class="card-label">官桥次数</div>
              <div class="card-data">{{ addrData.eraBridge }}</div>
            </el-card>
          </el-col>
          <el-col :span="6">
            <el-card>
              <div class="card-label">官桥金额</div>
              <div class="card-data">{{ addrData.bridge }}</div>
            </el-card>
          </el-col>
          <el-col :span="6">
            <el-card>
              <div class="card-label">zkLite Tx</div>
              <div class="card-data">{{ addrData.liteTx }}</div>
            </el-card>
          </el-col>
          <el-col :span="6">
            <el-card>
              <div class="card-label">zkLite官桥金额</div>
              <div class="card-data">{{addrData.liteBridge}}</div>
            </el-card>
          </el-col>
        </el-row>
        <el-row :gutter="18" class="row-3">
          <el-col :span="6">
            <el-card>
              <div class="card-label">激活天</div>
              <div class="card-data">{{ addrData.age }}</div>
            </el-card>
          </el-col>
          <el-col :span="6">
            <el-card>
              <div class="card-label">活跃月</div>
              <div class="card-data">{{ addrData.month }}</div>
            </el-card>
          </el-col>
          <el-col :span="6">
            <el-card>
              <div class="card-label">活跃周</div>
              <div class="card-data">{{ addrData.week }}</div>
            </el-card>
          </el-col>
          <el-col :span="6">
            <el-card>
              <div class="card-label">活跃天</div>
              <div class="card-data">{{ addrData.day }}</div>
            </el-card>
          </el-col>
        </el-row>
        <el-row :gutter="18" class="row-4">
          <el-col :span="6">
            <el-card>
              <div class="card-label">空投分数</div>
              <div class="card-data">{{ addrData.score }}</div>
            </el-card>
          </el-col>
          <el-col :span="6">
            <el-card>
              <div class="card-label">空投排名</div>
              <div class="card-data">{{ addrData.rank }}</div>
            </el-card>
          </el-col>
          <el-col :span="6">
            <el-card>
              <div class="card-label">空投前%</div>
              <div class="card-data">{{ addrData.percent }}</div>
            </el-card>
          </el-col>
          <el-col :span="6">
            <el-card>
              <div class="card-label">钱包质量</div>
              <div class="card-data card-data-btn" @click="dialog3=true">{{ addrData.quality }}</div>
            </el-card>
          </el-col>
        </el-row>
      </div>
      <div class="wrap-list">
        <el-card>
          <div class="contract-title">合约记录</div>
          <div class="contract-label">记录了主流dapp的交互记录，并非包括所有dapp记录。</div>
          <el-divider></el-divider>
          <div class="table-list">
            <el-table :data="contractData" style="width: 100%">
              <el-table-column prop="name" label="dapp" width="250">
                <template slot-scope="scope">
                  <a :href="scope.row.url" target="_blank" style="text-decoration:none;color:inherit;">
                    <div class="dapp-button">
                      <img :src='"/static/protocol/" + scope.row.id + ".png"'  alt=""/>
                      <div>{{ scope.row.name }}</div>
                    </div>
                  </a>
                </template>
              </el-table-column>
              <el-table-column prop="tx" label="tx" align="center">
                <template slot-scope="scope">
                  <span class="tx-color" v-if="scope.row.tx > 0">{{ scope.row.tx }}</span>
                  <span v-else>{{ scope.row.tx }}</span>
                </template>
              </el-table-column>
              <el-table-column prop="recent" label="上次使用" align="center">
                <template slot-scope="scope">
                  <span class="recent-color" v-if="scope.row.recent != '无'">{{ scope.row.recent }}</span>
                  <span v-else>{{ scope.row.recent }}</span>
                </template>
              </el-table-column>
              <el-table-column prop="erc20u" label="金额$" align="right">
                <template slot-scope="scope">
                  <span class="usd-color" v-if="scope.row.erc20u > 0">{{ scope.row.erc20u.toFixed(1) }}</span>
                  <span v-else>{{ scope.row.erc20u.toFixed(1) }}</span>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-card>
      </div>
      <div class="wrap-list">
        <el-card>
          <div class="contract-title">合约记录</div>
          <div class="contract-label">记录了不在上述dapp内的合约地址交互记录。</div>
          <el-divider></el-divider>
          <div class="table-list">
            <el-table :data="newContractData" style="width: 100%">
              <el-table-column prop="name" label="合约地址" width="250">
                <template slot-scope="scope">
                  <a :href="'https://explorer.zksync.io/address/' + scope.row.contract" target="_blank" style="color:inherit;">{{ scope.row.scontract }}</a>
                </template>
              </el-table-column>
              <el-table-column prop="tx" label="tx" align="center">
                <template slot-scope="scope">
                  <span class="tx-color">{{ scope.row.tx }}</span>
                </template>
              </el-table-column>
              <el-table-column prop="recent" label="上次使用" align="center">
                <template slot-scope="scope">
                  <span class="recent-color">{{ scope.row.recent }}</span>
                </template>
              </el-table-column>
              <el-table-column prop="erc20u" label="金额$" align="right">
                <template slot-scope="scope">
                  <span class="usd-color">{{ scope.row.erc20u.toFixed(1) }}</span>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-card>
      </div>
      <el-dialog title="表格数据说明" :visible.sync="dialog3" width="30%">
        <p><strong>数据来源：</strong>zksync官方区块浏览器，实时数据。</p>
        <p><strong>空投分数：</strong>参照arb积分制排名，共20分，具体规则如下：</p>
        <ul>
          <li>精品号：排名<10w</li>
          <li>路人号：排名<30w</li>
          <li>保底号：排名<50w</li>
          <li>反撸号：排名>50w</li>
        </ul>
        <ul>
          <li>跨链到zkSync Lite</li>
          <li>在zkSync Lite上交互10次</li>
          <li>在zkSync Lite上交互25次</li>
          <li>跨链到zkSync Era</li>
          <li>使用过跨链桥跨过$10,000资产</li>
          <li>使用过跨链桥跨过$25,000资产</li>
          <li>使用过跨链桥跨过$50,000资产</li>
          <li>使用过跨链桥跨过$100,000资产</li>
          <li>在zkSync Era上交互10次</li>
          <li>在zkSync Era上交互25次</li>
          <li>在zkSync Era上交互100次</li>
          <li>与超过10个合约交互过</li>
          <li>与超过25个合约交互过</li>
          <li>与超过50个合约交互过</li>
          <li>在3个独立月使用过</li>
          <li>在6个独立月使用过</li>
          <li>在9个独立月使用过</li>
          <li>在10个独立周使用过</li>
          <li>在25个独立周使用过</li>
          <li>在50个独立周使用过</li>
        </ul>
      </el-dialog>
    </div>
  </el-main>
</template>

<script>
import {getEthBalance as getBalance, getZkLiteTx, getZkSyncTx} from "../../../utils/getblock";
import {calcZksScore} from "../../../utils/calcscore";
import axios from "axios";

export default{
  name: "Analysis",
  data() {
    return {
      wallet: "",
      addrData: [],
      contractData: [],
      newContractData: [],
      dialog3: false,
      totalAccount: 2975372,
      newAccount: 0,
      okApi: "ad4621f2-8dbe-41fa-b60f-f7856cbe1390"
    }
  },
  mounted() {
    // 获取基本信息
    this.getBlockInfo();
  },
  methods: {
    // 获取基本信息
    getBlockInfo() {
      const config = {
        headers: {
          "Ok-Access-Key": this.okApi
        },
        params: {
          "chainShortName": "zksync"
        }
      };
      axios.get("https://www.oklink.com/api/v5/explorer/blockchain/address", config).then(res => {
        const ret = res.data.data[0];
        this.totalAccount = ret["totalAddresses"];
        this.newAccount = ret["newTotalAddresses24h"];
      })
    },
    // 获取数据
    fetchData() {
        let addr = this.wallet;
        this.addrData['saddr'] = addr.substring(0,6) + "..." + addr.substring(addr.length-4, addr.length);
        getBalance("ethereum", addr).then(res => {
          this.addrData['balance'] = res;
        });
        getBalance("zksyncera", addr).then(res => {
          this.addrData['ethEra'] = res;
        });
        // era: recent, contracts, months, weeks, days, age, fee
        getZkSyncTx(addr).then(res => {
          this.addrData['recent'] = res[0];
          this.addrData['srecent'] = res[1];
          this.addrData['contract'] = res[2];
          this.addrData['month'] = res[3];
          this.addrData['week'] = res[4];
          this.addrData['day'] = res[5];
          this.addrData['age'] = res[6];
          this.addrData['fee'] = res[7].toFixed(4);
          this.addrData['eraTx'] = res[8];
          this.addrData['eraBridge'] = res[9];
          this.addrData['bridge'] = '$' + res[10];
          this.contractData = res[11][0];
          this.newContractData = res[11][1];
          if (!!this.addrData['liteTx']) {
            let ret = calcZksScore(this.addrData, this.totalAccount);
            this.addrData['score'] = ret[0];
            this.addrData['rank'] = ret[1];
            this.addrData['percent'] = ret[2] + "%";
            this.addrData['quality'] = ret[3];
          }
          this.$forceUpdate();
        });
        // lite: liteBridge, liteTx
        getZkLiteTx(addr).then(res => {
          this.addrData['liteTx'] = res[0];
          this.addrData['liteBridge'] = '$' + res[1];
          if (!!this.addrData['eraTx']) {
            let ret = calcZksScore(this.addrData, this.totalAccount);
            this.addrData['score'] = ret[0];
            this.addrData['rank'] = ret[1];
            this.addrData['percent'] = ret[2] + "%";
            this.addrData['quality'] = ret[3];
          }
          this.$forceUpdate();
        });
      }
  }
}
</script>

<style lang="stylus" scoped>
$color1 = #3651f1
$color2 = #d44ec2
$color3 = #d28905
$color4 = #07ab22
$textColor = #606266
$greyColor = #e6e6e6

.wrap-header
  display flex
  padding-bottom 10px
  margin-bottom 20px
  font-size 14px
  border-bottom solid 1px $greyColor
  .item
    margin-right 30px
    color $color4
    span
      color $textColor

.dapp-button
  display flex
  img
    width 20px
    height 20px
    border-radius 10px
    margin-right 5px

.tx-color
  color $color1
  font-weight bold
.recent-color
  color $color3
  font-weight bold
.usd-color
  color $color2
  font-weight bold

.contract-title
  font-weight bold
  font-size 20px
  color $color1
.contract-label
  color $textColor
  margin-top 4px

.row-1
  .card-label
    font-size 14px
    color $textColor
    margin-bottom 5px
  .card-data
    font-size 26px
    color $color1
    font-weight bold

.row-2
  margin-top 18px
  .card-label
    font-size 14px
    color $textColor
    margin-bottom 5px
  .card-data
    font-size 26px
    color $color2
    font-weight bold

.row-3
  margin-top 18px
  .card-label
    font-size 14px
    color $textColor
    margin-bottom 5px
  .card-data
    font-size 26px
    color $color3
    font-weight bold

.row-4
  margin-top 18px
  .card-label
    font-size 14px
    color $textColor
    margin-bottom 5px
  .card-data
    font-size 26px
    color $color4
    font-weight bold
  .card-data-btn:hover
    cursor pointer

.el-main
  width 800px
  margin 0 auto
  h3
    text-align center
  .header-input
    margin-left 150px
    .el-input
      width 400px
      margin-right 10px
  .header-select
    margin-left 150px
    margin-top 20px
    .el-select
      width 400px
  .wrap-data
    margin-top 20px
  .wrap-list
    margin-top 20px

</style>



